@use "src/sass/variables" as *;
@use "src/sass/mixins";

.game {
    display: flex;
    gap: 1rem;
    user-select: none;
    
    &__cell {
        background-color: white;
        border-right: $tableBorder;
        border-bottom: $tableBorder;
        width: $cellSize;
        height: $cellSize;
        position: relative;

        &_allowed::after, &_denied::after {
            content: " ";
            @include mixins.overlay;
            display: block;
            opacity: 0.5;
        }

        &_allowed::after {
            background-color: $allow;
        }

        &_denied::after {
            background-color: $deny;
        }
    }

    &__label, &__angle {
        background-color: #efefef;
    }

    &__field {
        &_missed {
            @include mixins.background('../../images/missed.svg');
        }
        &_ship {
            @include mixins.background('../../images/ship.svg');
        }
        &_sinked {
            @include mixins.background('../../images/sinked.svg');
        }
    }

    &__board {
        position: relative;
        display: flex;
        flex-direction: column;

        table {
            border-top: $tableBorder;
            border-left: $tableBorder;
            border-spacing: 0;
        }
    }

    &__status {
        padding: 0.5rem 1rem;
        display: flex;
        justify-content: center;
        gap: 1rem;
        font-size: 1.2rem;
        z-index: 1;
    }

    &__overlay {
        @include mixins.overlay;
        background-color: $overlayBackground;
        transition: background 1s ease;
        justify-content: center;
        align-items: center;

        &_wait-player, &_wait-step, &_active {
            display: flex;
        }

        &_wait-player::after, &_wait-step::after {
            content: " ";
            display: block;
            width: $overlayIconSize;
            height: $overlayIconSize;
        }

        &_wait-player::after {
            @include mixins.background('../../images/rotate.svg');
            animation: rotate 4s linear infinite;
        }

        &_wait-step::after {
            @include mixins.background('../../images/clock.svg');
        }
    }

    &__fleet {
        h2, p {
            text-align: left;
            margin: 0;
            padding-left: $fleetAmountSize;
        }
        p {
            margin-top: 0.25rem;
            margin-bottom: 1rem;
        }
    }

    &__fleet-item {
        display: flex;
        align-items: center;
        padding: 0.5rem;
        cursor: pointer;

        span {
            width: $fleetAmountSize;
            font-size: 1.2rem;
        }

        &_selected {
            background-color: $allow;
        }

        &_placed {
            opacity: 0.2;
        }
    }

    &__ship {
        --shipSize: 1;
        @include mixins.background('../../images/ship.svg');
        background-repeat: repeat;
        width: calc(var(--shipSize) * #{$cellSize});
        height: $cellSize;
    }
}

@keyframes rotate {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg) }
}