@use "src/sass/variables" as *;
@use "src/sass/mixins";

.app {
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.header {
  @include mixins.container;
  justify-content: space-between;
}

.header__title {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
}

.header__history {
  @include mixins.button;
}

.header__profile {
  @include mixins.button;
}

.main {
  @include mixins.container;
  flex-grow: 1;
  justify-content: center;
}

.footer {
  @include mixins.container;
  justify-content: flex-start;
  gap: 0.5rem;

  &__logo {
    display: block;
    height: 1.2rem;
  }
}

.winner {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  text-align: center;

  &__button {
    @include mixins.primaryButton;
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;

  &__input {
    padding: 0.5rem 1rem;
    line-height: 1.2rem;
    font-size: 1.2rem;
    text-align: center;
  }

  &__button {
    @include mixins.primaryButton;
  }
}