@use "src/sass/variables" as *;
@use "src/sass/mixins";

.modal {
  @include mixins.overlay;
  display: flex;
  background-color: $overlayBackground;
  z-index: 100;
  justify-content: center;
  align-items: center;

  &__container {
    width: 60vmin;
    background: $background;
    border: $tableBorder;
    padding: 1rem 2rem;
    margin-top: -30%;
  }
}